
'use client';

import React, { createContext, useContext, useEffect, useState, ReactNode } from 'react';
import { onAuthStateChanged, User } from 'firebase/auth';
import { auth, db } from '@/lib/firebase';
import { doc, onSnapshot } from 'firebase/firestore';
import type { User as FirestoreUser } from '@/functions/src/types';

interface AuthContextType {
  user: User | null;
  firestoreUser: FirestoreUser | null;
  loading: boolean;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [user, setUser] = useState<User | null>(null);
  const [firestoreUser, setFirestoreUser] = useState<FirestoreUser | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // This is the key listener that fires when auth state changes
    const unsubscribe = onAuthStateChanged(auth, (authUser) => {
      setUser(authUser);
      // If the user logs out, we clear their data and stop loading
      if (!authUser) {
        setFirestoreUser(null);
        setLoading(false);
      }
    });
    // Cleanup the auth listener on component unmount
    return () => unsubscribe();
  }, []);

  useEffect(() => {
    // This effect runs when the authUser object changes
    if (user) {
      // We have a user, so we start listening for their profile document.
      // We keep loading until we get it.
      setLoading(true);
      const userDocRef = doc(db, 'users', user.uid);
      const unsubscribe = onSnapshot(userDocRef, (doc) => {
        if (doc.exists()) {
          // We found the document! Set the user data and stop loading.
          setFirestoreUser(doc.data() as FirestoreUser);
          setLoading(false);
        }
        // If the document doesn't exist yet, we do nothing.
        // The onSnapshot listener will fire again when the document is created.
        // setLoading(false) is NOT called here, so we continue to wait.
      }, (error) => {
        // Handle any errors during the subscription
        console.error("Error subscribing to user document:", error);
        setFirestoreUser(null);
        setLoading(false);
      });
      
      // Cleanup the Firestore listener when the user logs out
      return () => unsubscribe();
    }
  }, [user]); // This effect depends on the 'user' object

  const value = { user, firestoreUser, loading };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};
